.tripList {

  & > ul {
    list-style: none;
    padding-inline-start: 0;
    margin: 0;

    & > li {
      margin-bottom: 1em;
    }

    & ul li {
      margin: 0.33em !important;
    }
  }
}

.breakdownTable {
  border-spacing: 0;
  margin-bottom: 1em;
  background-color: #efefef;
  padding: 0 0.5em;
  border-radius: 12px;
  width: 100%;

  td, th {
    padding: 12px 8px 12px 4px;
  }

  tr > td, tr:not(:first-child) > th {
    border-top: 1px solid gray;
  }

  tr:last-child {
    // font-size: 20px;

    td, th {
      border-top: 1px solid gray;
      margin: 0.5em inherit;
      padding: 0.5em inherit;
      font-weight: 800;
    }
  }
}

.tableBlock {
  display: flex;
  justify-content: center;
  flex-direction: column;
  border-top: 1px solid black;
  margin-top: 1.5em;

  img {
    display: none;
  }

  @media (min-width: 990px) {
    justify-content: center;
    .addOnList {
      width: 244px;
    }

    img {
      display: inline-block;
      height: 340px;
      width: 244px;
      align-self: center;
    }
  }
}

.buttonBlock {
  display: flex;
  flex-direction: column;
  gap: 8px;
  margin-bottom: 2em;
}

@media (min-width: 550px) {
  .buttonBlock {
    flex-direction: row;
    justify-content: center;

    button {
      flex: 0 1 200px;
    }
  }
}

@media (min-width: 640px) {
  .tableBlock {
    flex-direction: row;
  }
}