.vehicleTile {
  display: flex;
  flex-direction: row;
  text-align: left;
  background-color: transparent;
  border: none;
  box-shadow: none;
  padding: 0;
  padding-right: 6px;
  height: auto;
  align-items: center;

  .image {
    max-width: 96px;
    max-height: 76px;
    margin: 6px;
  }

  .textBlock {
    display: flex;
    flex-direction: column;
    flex: 1 1 0;
    justify-content: space-between;
    padding: 5px 0;

    h5 {
      margin: 0;
      color: inherit;
    }
  
    & > div {
      display: flex;
      flex-direction: row;

      .price {
        flex: 1 1 0;
        font-weight: 800;
      }
    }
  }
}