.breakdownTable {
  border-spacing: 0;
  margin-bottom: 1em;
  background-color: #efefef;
  padding: 0 0.5em;
  border-radius: 12px;
  width: 100%;

  td, th {
    padding: 12px 8px 12px 4px;
  }

  tr > td, tr:not(:first-child) > th {
    border-top: 1px solid gray;
  }

  tr:last-child {
    // font-size: 20px;

    td, th {
      border-top: 1px solid gray;
      margin: 0.5em inherit;
      padding: 0.5em inherit;
      font-weight: 800;
    }
  }
}