.outer {
  display: flex;
  flex-direction: column;
  gap: 16px;

  .stripeContainer > form {
    display: flex;
    flex-direction: column;
    gap: 10px;
    padding-bottom: 1em;
  }

  .buttonBlock {
    display: flex;
    gap: 8px;
    width: 100%;
    margin-top: 8px;

    & > button {
      flex: 1 1 0;
    }
  }
}

@media (min-width: 990px) {
  .outer {
    flex-direction: row;

    & > * {
      flex: 1 1 0;
    }
  }
}