.routeStep {
  display: flex;
  text-align: left;
  gap: 8px;
  align-items: start;
  flex-wrap: wrap;

  & > * {
    width: 100%;

    &:nth-child(1), &:nth-child(2) {
      flex: 1 1 30%;
    }
  }
}

.nextButton {
  flex: 1 1 100%;
}

@media (min-width: 480px) {
  .routeStep .dateTimeItem {
    flex: 1 1 30%;
  }

  .nextButton {
    align-self: center;
    width: 400px;
  }
}

@media (min-width: 640px) {
  .routeStep .addressItem {
    flex: 1 1 45%;
    width: auto !important;
  }
}

@media (min-width: 990px) {
  .routeStep {
    .tripTypeItem {
      flex-basis: 8em;
      flex-grow: 0;
    }
    .dateTimeItem {
      flex-basis: 10em;
      flex-grow: 0;
    }
    .addressItem {
      flex-basis: auto;
    }
  }
}