.widget {
  display: inline-block;
  width: 96%;
  max-width: 990px;
  text-align: left;
}


.map {
  flex: 1 1 0;
  width: 100%;
  padding-bottom: 1em;

  iframe {
    border: none;
    height: 60vw;
  }
}

.vehicleOuter {
  display: flex;
  flex-direction: column;
  margin-top: 1em;

  .vehicleList {
    display: flex;
    flex-direction: column;
    overflow-y: auto;
    flex: 1 1 0;

    :global(.ant-radio-button-wrapper:not(.ant-radio-button-wrapper-checked)) {
      background-color: #efefef;
    }
  }
}

:global(input.ant-input) {
  padding: 6px 12px;
}

.buttonOuter {
  display: flex;
  justify-content: center;
  gap: 12px;

  button {
    flex: 0 1 200px;
    margin-bottom: 1.5em;
  }
}

@media (min-width: 640px) {
  .vehicleOuter .vehicleList {
    flex-direction: row;
    justify-content: center;

    & > * {
      flex: 1 1 0;
    }
  }
}

@media (min-width: 768px) {
  .vehicleOuter .vehicleList {
    gap: 16px !important;
  }

  .map iframe {
    height: 50vw;
  }
}

@media (min-width: 990px) {
  .vehicleOuter .vehicleList {
    gap: 16px !important;
  }

  .map iframe {
    height: 450px;
  }
}