.flexRow {
  margin: 0 0 0.5em;
  display: flex;
  justify-content: space-between;
  gap: 12px;
  align-items: center;
}

.lineItem {
  .lineItemName {
    padding: 2px 4px 2px;
    text-align: right;
  }
}

.tripItemOuter {
  margin-top: 1em;
  text-align: center;

  & + &::before {
    content: ' ';
    border-top: 1px solid black;
    width: 66%;
    max-width: 400px;
    display: inline-block;
    margin: 0.5em 0 1em;
  }
}

.secondRow {
  display: flex;
  flex-direction: column;
  gap: 12px;
  margin-top: 12px;
}

@media (min-width: 360px) {
  .secondRow {
    flex-direction: row;
    flex-wrap: wrap;

    .vehicleButton {
      flex: 1 1 100%;
    }

    .actionButton {
      flex: 1 1 0;
    }
  }
}

@media (min-width: 640px) {
  .secondRow {
    flex-direction: row;
    flex-wrap: nowrap;

    .vehicleButton {
      flex: 1 1 320px;
      max-width: 320px;
      margin-right: auto;
    }

    .actionButton {
      flex: 1 1 0;
      max-width: 220px;

      &.deleteButton {
        max-width: 160px;
      }
    }
  }
}